.trusted {
  padding: var(--space-5) 0 var(--space-4);
}

.logoMarquee {
  position: relative;
  overflow: hidden;
  border-radius: var(--radius-md);
  padding: var(--space-2) 0;
  background: #f7f4f0;
}

.logoMarquee::before,
.logoMarquee::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 80px;
  pointer-events: none;
  z-index: 1;
}

.logoMarquee::before {
  left: 0;
  background: linear-gradient(90deg, #f7f4f0 0%, rgba(247, 244, 240, 0) 100%);
}

.logoMarquee::after {
  right: 0;
  background: linear-gradient(270deg, #f7f4f0 0%, rgba(247, 244, 240, 0) 100%);
}

.logoTrack {
  display: flex;
  gap: var(--space-2);
  align-items: center;
  width: max-content;
  color: var(--muted);
  animation: marquee 24s linear infinite;
}

.logoTrack svg {
  width: 160px;
  height: 46px;
  flex-shrink: 0;
}

@keyframes marquee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

@media (prefers-reduced-motion: reduce) {
  .logoTrack {
    animation: none;
  }
}
