.hero {
  position: relative;
  padding: var(--space-6) 0 var(--space-5);
  overflow: hidden;
}

.hero::before {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at 20% 20%, rgba(255, 255, 255, 0.7), transparent 60%),
    radial-gradient(circle at 80% 10%, rgba(0, 0, 0, 0.08), transparent 50%),
    linear-gradient(160deg, rgba(255, 255, 255, 0.6), rgba(208, 199, 189, 0.4));
  opacity: 0.9;
  pointer-events: none;
}

.hero::after {
  content: "";
  position: absolute;
  inset: 0;
  background-image: repeating-linear-gradient(
    135deg,
    rgba(17, 17, 17, 0.03),
    rgba(17, 17, 17, 0.03) 1px,
    transparent 1px,
    transparent 5px
  );
  pointer-events: none;
}

.heroGrid {
  position: relative;
  display: grid;
  gap: var(--space-4);
  z-index: 1;
}

.heroContent h1 {
  font-size: clamp(2.4rem, 5vw, 3.8rem);
  line-height: 1.1;
  margin-bottom: var(--space-3);
}

.heroSubhead {
  font-size: 1.1rem;
  color: var(--muted);
  margin-bottom: var(--space-3);
  max-width: 40ch;
}

.heroActions {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-2);
  margin-bottom: var(--space-3);
}

.trustRow {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-2);
  font-size: 0.95rem;
  color: var(--muted);
}

.heroVisual {
  position: relative;
  border-radius: var(--radius-lg);
  background: linear-gradient(160deg, #f6f4f2 10%, #e0d7cd 100%);
  min-height: 320px;
  overflow: hidden;
  box-shadow: var(--shadow);
}

.heroShapeA {
  position: absolute;
  width: 180px;
  height: 180px;
  background: rgba(17, 17, 17, 0.08);
  top: 30px;
  left: 20px;
  border-radius: 40% 60% 55% 45% / 45% 45% 55% 55%;
  filter: blur(2px);
}

.heroShapeB {
  position: absolute;
  width: 220px;
  height: 220px;
  background: rgba(17, 17, 17, 0.12);
  bottom: -40px;
  right: -20px;
  border-radius: 40% 60% 55% 45% / 45% 45% 55% 55%;
  filter: blur(2px);
}

.heroCard {
  position: absolute;
  left: 24px;
  bottom: 24px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: var(--radius-md);
  padding: var(--space-2);
  width: 180px;
  border: 1px solid rgba(17, 17, 17, 0.08);
}

.heroCardLabel {
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.heroCardLines {
  height: 6px;
  background: rgba(17, 17, 17, 0.12);
  border-radius: 999px;
  margin-bottom: 0.4rem;
}

.heroCardLinesShort {
  width: 60%;
}

.heroTower {
  position: absolute;
  right: 40px;
  bottom: 30px;
  width: 80px;
  height: 200px;
  background: linear-gradient(180deg, #ffffff 0%, #d9d2c8 100%);
  border-radius: 30px;
  border: 1px solid rgba(17, 17, 17, 0.1);
  box-shadow: var(--shadow);
}

.heroTowerSlim {
  right: 130px;
  width: 40px;
  height: 160px;
  opacity: 0.8;
}

@media (min-width: 768px) {
  .heroGrid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    align-items: center;
  }
}
