.differentiators {
  padding: var(--space-5) 0;
}

.diffGrid {
  display: grid;
  gap: var(--space-3);
}

.featureCard {
  background: var(--card);
  border: 1px solid var(--accent);
  border-radius: var(--radius-md);
  padding: var(--space-3);
  box-shadow: 0 8px 18px rgba(17, 17, 17, 0.04);
  transition: transform var(--motion-medium) var(--ease),
    box-shadow var(--motion-medium) var(--ease);
}

.featureCard h3 {
  margin-bottom: var(--space-1);
}

.featureCard p {
  color: var(--muted);
}

.featureCard:hover {
  transform: translateY(-4px);
  box-shadow: 0 18px 32px rgba(17, 17, 17, 0.12);
}

.diffVisual {
  position: relative;
  border-radius: var(--radius-lg);
  min-height: 260px;
  background: linear-gradient(135deg, #f6f4f2, #e7dfd6);
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--accent);
}

.stackBase,
.stackMid,
.stackTop {
  position: absolute;
  border-radius: var(--radius-sm);
  border: 1px solid rgba(17, 17, 17, 0.1);
  background: #ffffff;
}

.stackBase {
  width: 200px;
  height: 120px;
  bottom: 40px;
}

.stackMid {
  width: 170px;
  height: 90px;
  bottom: 80px;
  left: 40px;
}

.stackTop {
  width: 120px;
  height: 70px;
  bottom: 120px;
  left: 70px;
}

.stackBadge {
  position: absolute;
  top: 30px;
  right: 30px;
  padding: 0.4rem 0.8rem;
  border-radius: 999px;
  background: var(--text);
  color: var(--bg);
  font-size: 0.75rem;
}

@media (min-width: 768px) {
  .diffGrid {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }

  .diffVisual {
    grid-column: 2 / 3;
    grid-row: 1 / 3;
    min-height: 320px;
  }
}
