.bundle {
  padding: var(--space-5) 0;
}

.bundleGrid {
  display: grid;
  gap: var(--space-4);
}

.bundleContent h2 {
  margin-bottom: var(--space-2);
}

.bundleContent p {
  color: var(--muted);
}

.bundleList {
  list-style: none;
  margin: var(--space-3) 0;
  color: var(--muted);
}

.bundleList li {
  padding: 0.4rem 0;
  border-bottom: 1px solid var(--accent);
}

.bundleNote {
  margin-bottom: var(--space-3);
  color: var(--muted);
}

.bundleVisual {
  background: linear-gradient(160deg, #f6f4f2, #e0d7cd);
  border-radius: var(--radius-lg);
  padding: var(--space-4);
  min-height: 240px;
  display: grid;
  gap: var(--space-2);
  align-content: center;
  border: 1px solid var(--accent);
  transition: transform var(--motion-medium) var(--ease),
    box-shadow var(--motion-medium) var(--ease);
}

.bundleVisual:hover {
  transform: translateY(-4px);
  box-shadow: 0 18px 32px rgba(17, 17, 17, 0.12);
}

.bundleBlock {
  height: 50px;
  border-radius: var(--radius-sm);
  background: #ffffff;
  border: 1px solid rgba(17, 17, 17, 0.1);
}

.bundleBlockWide {
  height: 80px;
}

@media (min-width: 768px) {
  .bundleGrid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    align-items: center;
  }
}
