.faq {
  padding: var(--space-5) 0 var(--space-6);
}

.faqList {
  display: grid;
  gap: var(--space-2);
}

.faqItem {
  background: var(--card);
  border-radius: var(--radius-md);
  border: 1px solid var(--accent);
  padding: 1rem 1.2rem;
}

.faqItem summary {
  cursor: pointer;
  font-weight: 600;
}

.faqContent {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: max-height var(--motion-slow) var(--ease),
    opacity var(--motion-medium) var(--ease);
}

.faqItem[open] .faqContent {
  max-height: 200px;
  opacity: 1;
}

.faqItem p {
  color: var(--muted);
  margin-top: 0.8rem;
}

@media (prefers-reduced-motion: reduce) {
  .faqContent {
    transition: none;
  }
}
