.comparison {
  padding: var(--space-5) 0;
  background: #f9f7f5;
}

.tableWrap {
  overflow-x: auto;
  border: 1px solid var(--accent);
  border-radius: var(--radius-md);
  background: var(--card);
}

.comparisonTable {
  width: 100%;
  border-collapse: collapse;
  min-width: 640px;
}

.comparisonTable th,
.comparisonTable td {
  padding: 1rem;
  border-bottom: 1px solid var(--accent);
  text-align: left;
}

.comparisonTable th {
  background: #f6f2ee;
  font-weight: 600;
}

.comparisonTable th:nth-child(2),
.comparisonTable td:nth-child(2) {
  background: #0f0f10;
  color: #f4f3f1;
  text-align: center;
}

.comparisonTable th:nth-child(n + 3),
.comparisonTable td:nth-child(n + 3) {
  text-align: center;
}

.comparisonTable th:nth-child(2) {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.comparisonTable tbody tr:last-child td:nth-child(2) {
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.comparisonTable tbody tr:last-child td {
  border-bottom: none;
}

.comparisonCta {
  margin-top: var(--space-3);
  text-align: center;
}
