.reviews {
  padding: var(--space-5) 0;
  background: #f9f7f5;
}

.carousel {
  position: relative;
  display: flex;
  align-items: center;
  gap: var(--space-2);
}

.carouselTrack {
  overflow-x: auto;
  overflow-y: visible;
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 100%;
  gap: var(--space-2);
  width: 100%;
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  scrollbar-width: none;
}

.carouselTrack::-webkit-scrollbar {
  display: none;
}

.reviewCard {
  background: var(--card);
  border-radius: var(--radius-md);
  padding: var(--space-3);
  border: 1px solid var(--accent);
  box-shadow: var(--shadow);
  scroll-snap-align: start;
  display: flex;
  flex-direction: column;
  min-height: 240px;
  transition: transform var(--motion-medium) var(--ease),
    box-shadow var(--motion-medium) var(--ease);
}

.reviewCard:hover {
  transform: translateY(-4px);
  box-shadow: 0 18px 32px rgba(17, 17, 17, 0.12);
}

.reviewCard p {
  color: var(--muted);
  margin: var(--space-2) 0;
}

.stars {
  letter-spacing: 0.08em;
}

.reviewer {
  font-weight: 600;
  color: var(--text);
}

.reviewFooter {
  margin-top: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--space-2);
  font-size: 0.9rem;
  color: var(--muted);
}

.reviewSource {
  display: inline-flex;
  align-items: center;
  gap: 0.4rem;
  font-weight: 500;
}

.googleMark {
  width: 18px;
  height: 18px;
  display: block;
}

.carouselBtn {
  border: 1px solid var(--accent);
  background: var(--card);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.carouselDots {
  display: flex;
  justify-content: center;
  gap: 0.6rem;
  margin-top: var(--space-3);
}

.carouselDots button {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: none;
  background: var(--accent);
}

.carouselDots button[aria-current="true"] {
  background: var(--text);
}

@media (min-width: 768px) {
  .carouselTrack {
    grid-auto-columns: calc(100% / 2 - 0.5rem);
  }
}

@media (min-width: 1024px) {
  .carouselTrack {
    grid-auto-columns: calc(100% / 3 - 0.7rem);
  }
}
